.form {
    background-color: #ffffff;
    height: 0;
    width: 60%;
    margin-right: auto;
    margin-top: 0px;
    padding: 0px;
    text-align:left;
}

.formNew {
    background-color: #FFF;
    width: 40%;
    margin-left: auto;
    margin-top: 0px;
    padding: 0px;
}

.headerStyleAppointment{
    margin-left: 45px;
    color: #232323;
    font-size: 14px;
    margin-top: 1px;
}

.headerStyleScheduled{
    margin-left: 45px;
    color: #232323;
    font-size: 14px;
}

.label {
    margin-top: 12px;
    float:right;
    margin-bottom: 0px;
    font-family: sans-serif !important;
    color: #212529 !important;
    font-size: 15px !important;
}
.inputFeild {
    font-size: 14px !important;
}

.patientlabel{
    margin-top: 17px;
    float:right;
    margin-bottom: 0px;
    font-family: sans-serif !important;
    color: #212529 !important;
    font-size: 15px !important;
}
input{
    padding-left: 7px !important;
}
.input {
    height: 20px;
}

.dropDown {
    height: 30px;
    width: 463px;
    border-bottom: none;
    border: 1.5px solid #c9c9c9;
    margin-top: 10px;
    margin-left: 0px;
    font-size: 14px; background-color: white; color: black;
}


.buttonConfirm { 
    border: none;
    background-color: #669999;
    color: #ffffff;
    margin-left: 10px;
    border-radius: 3px ;
    padding: 8px 25px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;    
}

.buttonClear { 
    border: none;
    background-color: #e7e7dd;
    color: #232323;    
    border-radius: 3px;    
    padding: 8px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;    
}

.button {
    text-align:right
}

.tbltd{
    vertical-align: top;
    text-align: left;
}

.scheduledItem {
